// "import 'swiper/css';" –ø–æ—Ç—Ä—ñ–±–Ω–æ –¥–æ–¥–∞—Ç–∫–æ–≤–æ –¥–æ–¥–∞—Ç–∏ –≤ —Ñ–∞–π–ª js –ø—ñ—Å–ª—è "import Swiper from 'swiper';"
// –∞–±–æ
// "import 'swiper/css'; import 'swiper/css/navigation';
//  import 'swiper/css/pagination';"
// –ø–æ—Ç—Ä—ñ–±–Ω–æ –¥–æ–¥–∞—Ç–∫–æ–≤–æ –¥–æ–¥–∞—Ç–∏ –≤ —Ñ–∞–π–ª js –ø—ñ—Å–ª—è "import Swiper from 'swiper'; import { Navigation, Pagination } from 'swiper/modules';"
// –ø–æ—Ç—Ä—ñ–±–Ω–æ —á–∏—Ç–∞—Ç–∏ –¥–æ–∫—É–º–µ–Ω—Ç–∞—Ü—ñ—é, —è–∫–∏–π —Å–∞–º–µ –≤–∞—Ä—ñ–∞–Ω—Ç –ø–æ—Ç—Ä—ñ–±–µ–Ω

import Swiper from 'swiper';
import { Navigation, Autoplay, Keyboard } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';

// ===== —ñ–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è —Å–ª–∞–π–¥–µ—Ä–∞ =====
let swiper;

function initSwiper() {
  // —è–∫—â–æ –≤–∂–µ —ñ—Å–Ω—É—î –µ–∫–∑–µ–º–ø–ª—è—Ä ‚Äî –∑–Ω–∏—â—É—î–º–æ –ø–µ—Ä–µ–¥ –ø–æ–≤—Ç–æ—Ä–Ω–∏–º —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è–º
  if (swiper) {
    swiper.destroy(true, true);
  }

  swiper = new Swiper('.hero-slider', {
    modules: [Navigation, Keyboard, Autoplay],
    slidesPerView: 1,
    allowTouchMove: true,
    loop: false,
    autoplay: {
      delay: 5000,
      disableOnInteraction: true,
    },
    keyboard: {
      enabled: true,
      onlyInViewport: false,
    },
    navigation: {
      nextEl: '.slider-button.next',
      prevEl: '.slider-button.prev',
      disabledClass: 'slider-button--disabled',
    },
    on: {
      init() {
        updateButtons(this);
      },
      slideChange() {
        updateButtons(this);
      },
    },
  });
}

// ===== —Ñ—É–Ω–∫—Ü—ñ—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è —Å—Ç–∞–Ω—É –∫–Ω–æ–ø–æ–∫ =====
function updateButtons(swiper) {
  const prevBtn = document.querySelector('.slider-button.prev');
  const nextBtn = document.querySelector('.slider-button.next');

  prevBtn.disabled = swiper.isBeginning;
  nextBtn.disabled = swiper.isEnd;
}

// ===== –∑–∞–ø—É—Å–∫ –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ —Å—Ç–æ—Ä—ñ–Ω–∫–∏ =====
window.addEventListener('load', initSwiper);

// ===== –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –ø—Ä–∏ –∑–º—ñ–Ω—ñ —Ä–æ–∑–º—ñ—Ä—É –≤—ñ–∫–Ω–∞ (debounce) =====
let resizeTimeout;
window.addEventListener('resize', () => {
  clearTimeout(resizeTimeout);
  resizeTimeout = setTimeout(() => {
    initSwiper();
  }, 300);
});

const navButtons = document.querySelectorAll('.btn-arrow');

navButtons.forEach(btn => {
  btn.addEventListener('mouseup', () => btn.blur());
  btn.addEventListener('touchend', () => btn.blur());
});
